  border-radius: 17.5px;
  transition: all 0.15s ease-in-out;
  transform: translateY(-50%);
}
.e-switch__switcher::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    width: 20px;
    height: 20px;
    background-color: #DA4830;
    border-radius: 50%;
    transition: background-color .15s ease-in-out, left .2s linear, right .2s linear;
}
