.e-transaction {
  display: flex;
  min-height: 26px;
  box-sizing: border-box;
  transform: translate3d(0, 0, 0);
  position: relative;
  z-index: 1;
  border-bottom: 1px solid #F0F6F9;
  padding: 0;
}
.e-transaction.is-my {
    cursor: pointer;
}
.e-transaction.is-my:hover:before {
      background-color: #f3f6ff;
}
.e-transaction:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    border-radius: 2px;
    background-color: #fff;
    transition: all .08s ease-in-out;
}
.e-transaction__appear .e-transaction__bg {
    opacity: 0.12;
}
.e-transaction__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    border-radius: 2px;
    opacity: 0;
}
.e-transaction.e-transaction_sell .e-transaction__bg {
    background-color: #e93a3f;
}
.e-transaction.e-transaction_buy .e-transaction__bg {
    background-color: #17DA9D;
}
.e-transaction__col {
    white-space: nowrap;
    padding: 8px 3px;
    box-sizing: border-box;
    border-right: 1px solid #F0F6F9;
}
@media only screen and (max-width: 1439px) {
.e-transaction__col {
        padding: 8px 5px;
}
}
.e-transaction__market-name {
    display: inline-block;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 800;
    color: #0D105B;
    border: 1px solid #d9d9e2;
    border-radius: 4px;
    padding: 0 2px;
    line-height: 15px;
    margin-right: 2px;
    transition: border-color .15s ease-in-out, color .15s ease-in-out;
}
.e-transaction__market-name:hover {
      border-color: #0e30e2;
      color: #0e30e2;
}
.e-transaction__time {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    order: 4;
    flex: 0 0 54px;
    padding-right: 4px;
    padding-left: 4px;
    font-size: 11px;
}
.e-transaction__exchange {
    font-size: 10px;
    flex: 0 0 24px;
    order: 4;
    border-right: 0;
    text-align: center;
}
.e-transaction__time-text {
    position: relative;
